<template>
  <div class="js-container">
    <div class="article-list">
      <ul>
        <li v-for="(item,index) in aritcleList" :key="index">
          <h1>{{item.title}}</h1>
          <p>{{item.content}}</p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      aritcleList: []
    }
  },
  methods: {
    test: function () {
      this.$axios.get('/jsPage', {'page': 1}).then(res => {
        this.aritcleList.push({
          title: res.data.title,
          content: res.data.content
        })
      })
    }
  },
  mounted () {
    this.test()
  }
}
</script>



