import Vue from 'vue'
import Router from 'vue-router'
import homePage from '@/components/homePage'
import js from '@/components/js'
import css from '@/components/css'
import html from '@/components/html'
import nodejs from '@/components/nodejs'
import vue from '@/components/vue'
import adminPage from '@/components/adminPage'
import postbar from '@/components/postbar'
import selfCon from '@/components/selfCon'
import todolist from '@/components/todolist'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'homePage',
      redirect: '/homePage'
    },
    {
      path: '/homePage',
      name: 'homePage',
      component: homePage,
      children: [
        {
          path: 'js',
          name: 'js',
          component: js
        },
        {
          path: 'css',
          name: 'css',
          component: css
        },
        {
          path: 'html',
          name: 'html',
          component: html
        },
        {
          path: 'vue',
          name: 'vue',
          component: vue
        },
        {
          path: 'nodejs',
          name: 'nodejs',
          component: nodejs
        }
      ]
    },
    {
      path: '/todolist',
      name: 'todolist',
      component: todolist
    },
    {
      path: '/postbar',
      name: 'postbar',
      component: postbar
    },
    {
      path: '/adminPage',
      name: 'adminPage',
      component: adminPage
    },
    {
      path: '/selfCon',
      name: 'selfCon',
      component: selfCon
    }
  ]
})



