<template>
  <div class="container">
    <button class="login" id="login" @click="toggleLogin">登录</button>
    <div class="loginPageContainer" id="loginPageContainer">
      <div class="loginPage" id="loginPage" v-if="loginPageTemp">
        <form action="/login" method="post">
          用户名：<input type="text" id="username" name="username" placeholder="用户名"> <br/>
          密码: <input type="text" id="password" name="password" placeholder="密码"><br/>
          <input type="submit" value="登录">
          <a @click="toggleLogin">x</a>
        </form>
        <button type="button" @click="loginPageTemp = false">去注册</button>
      </div>
      <div class="loginPage" v-if="!loginPageTemp">
        <form action="/register" method="post">
          用户名：<input type="text" id="username" name="username" placeholder="用户名"><br/>
          密码: <input type="text" id="password" name="password" placeholder="密码"><br/>
          <input type="submit" value="注册">
        </form>
        <button type="button" @click="loginPageTemp = true">去登录</button>
      </div>
    </div>
    <div class="user-nav" id="user-nav">
      <h1>welcome</h1>
    </div>
    <h1 class="pageTitle" id="pageTitle">
      {{pageTitle}}
    </h1>
    <div id="container-head">
      <div id="head-img" class='head-img'>
        <img src="../assets/head.jpg">
      </div>
    </div>
    <div id="container-body">
      <div class="page-nav-left">
        <router-link to="/homePage/js"><div @click="navToggle(0)" class="nav-item">javaScript</div></router-link>
        <router-link to="/homePage/css"><div @click="navToggle(1)" class="nav-item">css3</div></router-link>
        <router-link to="/homePage/html"><div @click="navToggle(2)" class="nav-item">html5</div></router-link>
        <router-link to="/homePage/vue"><div @click="navToggle(3)" class="nav-item">vue</div></router-link>
        <router-link to="/homePage/nodejs"><div @click="navToggle(4)" class="nav-item">nodejs</div></router-link>
      </div>
      <div class="page-nav-right">
        <router-link to="/adminPage"><div @mouseover="hoverChange(0)" class="nav-item-right">管理中心</div></router-link>
        <div class="show-demo"></div>
        <router-link to="/postbar"><div @mouseover="hoverChange(1)" class="nav-item-right">留言板</div></router-link>
        <div class="show-demo"></div>
        <router-link to="/todolist"><div @mouseover="hoverChange(2)" class="nav-item-right">todolist</div></router-link>
        <div class="show-demo"></div>
        <router-link to="/selfCon"><div @mouseover="hoverChange(3)" class="nav-item-right">个人作品</div></router-link>
        <div class="show-demo"></div>
      </div>
    </div>
    <div id="content" class="content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      scrollTop: 0,
      scrollChange: true, // 切换是否在展开页面
      navColor: ['#dddddd', '#bbbbbb', '#999999', '#777777', '#555555'],
      navItem: ['javaScript', 'css3', 'hTML5', 'Vue', 'nodeJs'],
      navSelect: 0,
      pageTitle: 'javaScript',
      loginPageTemp: true
    }
  },
  methods: {
    // 过渡动画
    pageScroll: function () {
      var containerHead = document.getElementById('container-head')
      var content = document.getElementById('content')
      var headImg = document.getElementById('head-img')
      var navItems = document.getElementsByClassName('nav-item')
      var showDemo = document.getElementsByClassName('show-demo')
      var pageTitle = document.getElementById('pageTitle')
      if (this.scrollTop > 0 && this.scrollChange) {
        this.scrollChange = false
        containerHead.style.height = '60px'
        content.style.top = '0'
        content.style.height = '100%'
        headImg.style.opacity = '0'
        for (let i = 0; i < navItems.length; i++) {
          navItems[i].style.transform = 'rotate(45deg)'
        }
        pageTitle.style.opacity = '1'
      } else if (this.scrollTop === 0) {
        this.scrollChange = true
        containerHead.style.height = '40%'
        content.style.top = '30%'
        content.style.height = '70%'
        headImg.style.opacity = '1'
        for (let i = 0; i < navItems.length; i++) {
          navItems[i].style.transform = 'none'
          navItems[i].style.margin = '3px auto'
        }
        // 右侧预览窗口在scrollTop为0时不显示
        for (let i = 0; i < showDemo.length; i++) {
          showDemo[i].style.height = '0'
        }
        pageTitle.style.opacity = '0'
      }
      this.navToggle(this.navSelect)
    },
    handleScroll: function () { // 监听sroll改变
      this.scrollTop = document.getElementById('content').scrollTop
    },
    toggleLogin: function () { // 切换login显示
      var loginPageContainer = document.getElementById('loginPageContainer')
      loginPageContainer.style.display === 'block' ? loginPageContainer.style.display = 'none' : loginPageContainer.style.display = 'block'
    },
    hoverChange: function (val) { // 悬浮预览内容
      if (this.scrollTop !== 0) {
        var showDemo = document.getElementsByClassName('show-demo')
        for (let i = 0; i < showDemo.length; i++) {
          showDemo[i].style.height = '0'
        }
        showDemo[val].style.height = '300px'
      }
    },
    navToggle: function (val) { // 标签控制
      this.navSelect = val
      var content = document.getElementById('content')
      content.style.backgroundColor = this.navColor[val]
      if (this.scrollTop !== 0) {
        var navItems = document.getElementsByClassName('nav-item')
        for (let i = 0; i < navItems.length; i++) {
          navItems[i].style.margin = '40px auto'
        }
        navItems[val].style.margin = '0px 0px 0px 150px'
        this.pageTitle = this.navItem[val]
      }
    }
  },
  mounted () {
    document.getElementById('content').addEventListener('scroll', this.handleScroll)
    var navItems = document.getElementsByClassName('nav-item')
    for (let i = 0; i < navItems.length; i++) {
      navItems[i].style.backgroundColor = this.navColor[i]
    }
  },
  watch: {
    scrollTop: function () {
      this.pageScroll()
    }
  }

}
</script>

<style lang="less">
@import "../css/styles.less";
.container {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
#container-head {
  height: 40%;
  width: 100%;
  background-color: @mainthemeColor;
  transition: height 1s;
  margin: 0;
  padding: 0;
}
#container-body {
  position: relative;
  width: 100%;
  flex: 1;
  background-color: @containerColor;
  overflow: hidden;
}
.content {
  position: absolute;
  transition: top 1s, height 1s,background-color 1s;
  top: 30%;
  left: 20%;
  width: 60%;
  height: 70%;
  background-color: white;
  overflow: auto;
}
.lists-item {
  margin: 100px;
}
.head-img {
  display: block;
  transition: opacity 1s ease-in-out;
  background-color: @mainthemeColor;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.user-nav {
  position: absolute;
  top: 25%;
  right: 25%;
  z-index: inherit;
}
.login {
  width: 60px;
  height: 40px;
  position: fixed;
  top:1%;
  right: 1%;
  background-color: white;
  border: none;
  border-radius: 5px;
  /* 重叠时置高 */
  z-index: 1;
}
.loginPage {
  position: fixed;
  width: 500px;
  height: 300px;
  margin: -150px 0 0 -250px;
  top: 50%;
  left: 50%;
  background-color: @mainthemeColor;
  z-index: inherit;
}
.loginPageContainer {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /*设置半透明背景 */
  z-index: 1;
}
.page-nav-right {
  position: absolute;
  top: 50px;
  right: 2%;
  width: 15%;
}
.page-nav-left {
  position: absolute;
  top: 50px;
  left: 20px;
  width: 15%;
}
.nav-item-right {
  display: block;
  width: 100%;
  height: 40px;
  background-color: @mainthemeColor;
  text-align: center;
  margin: 0 0 10px 0;
  padding: 10px 0 0 0;

}
.nav-item {
  width: 100px;
  height: 100px;
  text-align: center;
  background-color:@mainthemeColor;
  margin: 3px auto;
  /* 旋转动画 */
  transition: transform 1s,margin 1s;
  /* transform: rotate(45deg); */
}
a, a:focus, s:hover {
  text-decoration:none;
  color: black;
}
.show-demo {
  display: true;
  background-color: @mainthemeColor;
  width: 100%;
  transition: height 0.5s;
  height: 0px;
  margin: 15px 0 15px 0 ;
  overflow: hidden;

}
.pageTitle {
  opacity: 0;
  position: absolute;
  top: 6px;
  left: 20px;
}
</style>



